@Component({
  selector: 'app-sync-jobs',
  templateUrl: './sync-jobs.component.html',
  styleUrls: ['./sync-jobs.component.css']
})
export class SyncJobsComponent implements OnInit {
  RType: SelectItem[];
  reportTypesOpt: any=[];
  criteria: boolean= false;
  job_keys: string;
  rtypeError: boolean;
  reportTypeData: any;
  rDate: Date;
  rTypeData: any;
  cols: any = [];
  freezeCols : any =[];
  scrollableCols : any [];
  cartdata: any =[];
  rowData: any = [];
  TotalRecived: any=[];
  TotalCompleted: any=[];
  auth: any = {};
  constructor(
    // private confirmationService: ConfirmationService,
    protected baseServices: BaseService,
    protected router: Router,
    protected httpService: HttpService,
    protected customModalPopService: CustomModalPopUpService,
    protected alert: AlertMessageService,
    private fb: FormBuilder) {
      //super(baseServices, router);
      this.auth = SessionObject.getUserDetails();
    }
  ngOnInit() {
    this.job_keys='';
    this.reportTypeData  = [
      { value: 0, label: 'Please Select' },
      { value: 1, label: 'Recived Jobs By Art Team' },
      { value: 2, label: 'Completed Jobs By Art Team' }
    ];
    this.cols = [
      
      { field: 'job_key', header: 'Old Job Key' ,tid:0},
      { field: 'Newjobkey', header: 'New Job Key' ,tid:0},
      { field: 'jobID', header: 'JobID' ,tid:0},
      { field: 'grade', header: 'Grade' ,tid:1},
      { field: 'module', header: 'Module'  ,tid:1},
      { field: 'batch', header: 'Batch' ,tid:1},
      { field: 'topic', header: 'Topic'  ,tid:1},
      { field: 'curriculum', header: 'Curriculum'  ,tid:1},
      { field: 'artcomplex', header: 'Art-Complexity'  ,tid:1},
      { field: 'artassion', header: 'Art-Assignment'  ,tid:1}
    ];
    this.cols = this.cols.map(d => ({ field: d.field, header: d.header, tid: this.cols.indexOf(d) }));
    this.freezeCols = this.cols.filter(d => d.tid < 1);
    this.scrollableCols = this.cols.filter(d => d.tid > 0);
    this.getRefreshJobs();
  }
  exportDataAsCSV(dt: any) {
      let OtherCol = [
          { field: 'job_key', header: 'Job Key' },
          { field: '_id', header: 'ACHJob ID' },
          { field: '', header: 'Stages'}
        ];
      dt.exportCSV({}, OtherCol);
  }
  getRefreshJobs(){
    const myheader = new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded');
    let body = new HttpParams();
    let self = this;
    this.httpService.extractPostData(CustomerServicesUrls.ARTLOG_JOBS_REFRESH_SHOW, body, { headers: myheader }).subscribe((data) => {
      debugger
      self.TotalRecived = data.filter(d=> d.artRorC == 'Inflow' );
      self.TotalCompleted = data.filter(d=> d.artRorC == 'Outflow' );
      console.log(data);
      self.cartdata = data;
      //d.killed = false;
      //this.alert.showAlertScucess([data.msg], 5000);
    });
  }
  filterData(){
      const myheader = new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded');
      let body = new HttpParams();
      let self = this;
      body = body.set('filters',  self.job_keys );
      // tslint:disable-next-line: max-line-length
      debugger
      this.httpService.extractPostData(CustomerServicesUrls.ARTLOG_JOBS_REFRESH, body, { headers: myheader }).subscribe((data) => {
        //debugger
        // self.TotalRecived = data.filter(d=> d.artRorC == 'Inflow' );
        // self.TotalCompleted = data.filter(d=> d.artRorC == 'Outflow' );
        // console.log(data);
        //self.cartdata = data;
        //d.killed = false;
        self.getRefreshJobs();
        this.alert.showAlertScucess([data.msg], 5000);
      });
  }
}