import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../../core/services/http.service';
import { BaseComponent } from '../../../core/base/base.component';
import { BaseService } from '../../../core/base/base.service';
import { Router } from '@angular/router';
import { DropdownDataModel, AlertMessageService } from '../../../component';
import { HttpHeaders, HttpParams } from '@angular/common/http';
import { CustomerServicesUrls } from '../../../core/shared/constant/url-constants/customer-services.constants';
import * as moment from 'moment';
import { d } from '@angular/core/src/render3';
import { SessionObject } from '../../../core/shared';

@Component({
  selector: 'app-art-dashboard',
  templateUrl: './art-dashboard.component.html',
  styleUrls: ['./art-dashboard.component.css']
})
export class ArtDashboardComponent implements OnInit {
  rowsmoveTags: any = [];
  criteria: boolean = false;
  TotalCompleted : any = [];
  TotalRecived: any =[];
  dcaSummary: any =[];
  overDuedt : any =[];
  highDt: any = [];
  mediumDt: any = [];
  lowDt: any = [];
  OptData: any = [];
  summaryOfDCATaskModel: boolean;
  bulkDataVerifyModal: boolean;
  cols: any = [];
  cartdata : any =[];
  auth: any = {};
  private gridApi;
  private gridColumnApi;
  constructor(
    // private confirmationService: ConfirmationService,
    protected baseServices: BaseService,
    protected router: Router,
    protected httpService: HttpService,
    protected alert: AlertMessageService,
    ) {
      //super(baseServices, router);
      debugger
      this.auth = SessionObject.getUserDetails();
    }

  ngOnInit() {
    this.auth = SessionObject.getUserDetails();
    this.cols = [
      { field: 'job_key', header: 'Job Key' },
      { field: 'name', header: 'Job Name' ,cellStyle: { color: 'red', 'background-color': 'green'} },
      { field: 'grade', header: 'Grade' , cellStyle: {color: 'red', 'background-color': 'green'}},
      { field: 'module', header: 'Module' },
      { field: 'batch', header: 'Batch' },
      { field: 'topic', header: 'Topic' },
      { field: 'cstage', header: 'Current Stage' },
      { field: 'job_active_stage.status', header: 'Completion Status' },
      { field: 'currentRTeam', header: 'CR Team' },
      { field: 'curriculum', header: 'Curriculum' },
      { field: 'revisionC', header: 'Revision Count' },
      { field: 'artcomplex', header: 'Art-Complexity' },
      { field: 'artassion', header: 'Art-Assignment' },
      { field: 'artRorC', header: 'Inflow/Outflow' },
      { field: 'batchCDate', header: 'Batch Completion Date' },
      { field: 'receiveddate', header: 'ArtTeam Recieved Date' },
      { field: 'mpsDueDate', header: 'MPS Due Date' },
      { field: 'artComplateDate', header: 'ArtTeam Completion Date' },
      { field: 'recivedCStage', header: 'ArtTeam Task Recived Status' },
      { field: 'artTeamStatus', header: 'ArtTeam Status' },
      { field: 'artTeamPriority', header: 'ArtTeam Priority' },
      { field: 'exceptionCategory', header: 'Exception Category' },
      { field: 'exception', header: 'Explanation' },
    ];
    //headerName
    this.cols=this.cols.map(d=> ({ field: d.field, headerName:d.header, cellStyle: { border:'1px solid #eaeaea !important'} }));
    const myheader = new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded');
      let body = new HttpParams();
      let self = this;
      body = body.set('filters',  'text');
      // tslint:disable-next-line: max-line-length
      this.httpService.extractPostData(CustomerServicesUrls.ARTLOG_DCASUMMARY, body, { headers: myheader }).subscribe((data) => {
        self.dcaSummary = data.map(d=> (
          { job_key:d.job_key,
            name:d.name,
            topic:d.topic,
            cstage : d.cstage,
            job_active_stagestatus:d.job_active_stage.status, 
            currentRTeam:d.currentRTeam ,
            curriculum : d.curriculum,
            revisionC:d.revisionC,
            artcomplex:d.artcomplex, 
            artassion:d.artassion,
            artRorC:d.artRorC,
            batchCDate:d.batchCDate,
            mpsDueDate:d.mpsDueDate,
            artComplateDate:d.artComplateDate,
            recivedCStage:d.recivedCStage,
            artTeamStatus:d.artTeamStatus,
            artTeamPriority:d.artTeamPriority,
            exceptionCategory:d.exceptionCategory,
            exception:d.exception,
            dateCreated:d.dateCreated,  
            receiveddate: d.receiveddate, 
            batch: (d.batch)?d.batch:'', 
            grade: (d.grade)?d.grade:'', 
            module:(d.module)?d.module:'',
          }));
        self.overDuedt = data.filter(d=> d.artTeamStatus =='WIP' && moment(d.mpsDueDate) > moment());
        
        self.highDt = self.getHighJobs();
        self.mediumDt = self.getMediumJobs()
        self.lowDt = self.getLowJobs();
        let dataTest = data.map(d=> ({ dateCreated:d.dateCreated,  receiveddate: new Date(d.receiveddate), batch: (d.batch)?d.batch:'', grade: (d.grade)?d.grade:'', module:(d.module)?d.module:'' }))
        dataTest.sort((a, b) =>{ return a.receiveddate - b.receiveddate; });
        let OptData = []; 
        let exitDt = dataTest[0].receiveddate;
        let  exitBatch = dataTest[0].batch;
        let exitGrade = dataTest[0].grade;
        let exitModule = dataTest[0].module;
        for(let ddt of dataTest) {
          if(moment(exitDt).format('YYYY-MM-DD')  == moment(ddt.receiveddate).format('YYYY-MM-DD') &&
          ddt.batch == exitBatch &&  ddt.grade == exitGrade && ddt.module == exitModule
          ){
            let CountDtTerms = dataTest.filter(d => moment(d.receiveddate).format('YYYY-MM-DD')== moment(exitDt).format('YYYY-MM-DD') && d.batch == exitBatch &&  d.grade == exitGrade && d.module == exitModule);
            for( let dddtt of CountDtTerms){
              dataTest.splice( dataTest.indexOf(dddtt), 1);
            }
            //dataTest= dataTest.filter(d => moment(d.receiveddate).format('YYYY-MM-DD')!= moment(exitDt).format('YYYY-MM-DD') && d.batch != exitBatch &&  d.grade != exitGrade && d.module != exitModule);
            OptData.push(
              { receiveddate: moment(ddt.receiveddate).format('YYYY-MM-DD'),  
              countdt: CountDtTerms.length,
              grade: ddt.grade,
              module: ddt.module,
              batch: ddt.batch 
            })
          } else {
            exitDt = ddt.receiveddate;
            exitBatch = ddt.batch;
            exitGrade = ddt.grade; 
            exitModule = ddt.module;
          }
        }
        this.OptData= OptData;
        //this.alert.showAlertScucess([data.msg], 5000);
      });
  }
  getDateWideSummary(dt){
    this.cartdata = this.dcaSummary.filter(d=> d.grade== dt.grade && d.module==dt.module && d.batch==dt.batch && d.receiveddate==dt.receiveddate);;
    this.summaryOfDCATask();
  }
  getHighJobs(){
   return this.dcaSummary.filter(d=> d.artTeamStatus =='WIP' && d.artTeamPriority=='High');
  }
  getLowJobs(){
    return this.dcaSummary.filter(d=> d.artTeamStatus =='WIP' && (d.artTeamPriority=='Low' || d.artTeamPriority==''  ));
  }
  getMediumJobs(){
    return this.dcaSummary.filter(d=> d.artTeamStatus =='WIP' && d.artTeamPriority=='Medium' );
  }
  onGridReady(params) {
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
  }
  exportAsCSV() {
    this.gridApi.exportDataAsCsv({
      processCellCallback: function (cell) {
        var cellVal = cell.value;
        if( !!cell.column.colDef.cellRenderer &&  (cell.column.colDef.headerName =="Estimated End Date" ||  cell.column.colDef.headerName=="Actual End Date") ) {
            cellVal = cell.column.colDef.cellRenderer({value:  cell.value});
        } 
        return cellVal;
    }   
  });
  }
  summaryOfDCATask(){
    this.summaryOfDCATaskModel =  true;
  }
  
  lowDataOfDCATastsummary(){
    this.cartdata = this.getLowJobs();
    this.summaryOfDCATask();
  }
  mediumDataOfDCATastsummary(){
    this.cartdata = this.getMediumJobs();
    this.summaryOfDCATask();
  }
  highDataOfDCATastsummary(){
    this.cartdata = this.getHighJobs();
    this.summaryOfDCATask();
  }
  overdueDataOfDCATastsummary(){
    this.cartdata = this.overDuedt;
    this.summaryOfDCATask();
  }
  allDataOfDCATastsummary(){
    this.cartdata = this.dcaSummary;
    this.summaryOfDCATask();
  }
}
